import Layout from '../common/Layout';
import { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';


function Join() {
	const name = 'Join';
	const title = 'Join';
	const subTitle = '';
	const expCaption = 'vogue      .';

	const history = useHistory();
	const initVal = {
		userid: '',
		pwd1: '',
		pwd2: '',
		name: '',
		email: '',
		comments: '',
	};

	const [Val, setVal] = useState(initVal);
	const [Err, setErr] = useState({});
	const [Submit, setSubmit] = useState(false);

	// 
	const check = (value) => {
		const errs = {};
		const eng = /[a-zA-Z]/;
		const num = /[0-9]/;
		const spc = /[~!@#$%^&*)]/;

		if (value.userid.length < 5) {
			errs.userid = ' 5  ';
		}
		if (
			value.pwd1.length < 5 ||
			!eng.test(value.pwd1) ||
			!num.test(value.pwd1) ||
			!spc.test(value.pwd1)
		) {
			errs.pwd1 = ' 5 , , ,   ';
		}
		if (value.pwd1 !== value.pwd2 || !value.pwd2) {
			errs.pwd2 = '   ';
		}
		if (value.name.length < 2) {
			errs.name = '   ';
		}
		if (value.edu === '') {
			errs.edu = '  .';
		}
		if (!value.gender) {
			errs.gender = ' .';
		}
		if (value.email.length < 8 || !/@/.test(value.email)) {
			errs.email = ' 8  @ ';
		}
		if (!value.favorite) {
			errs.favorite = '  .';
		}
		if (value.comments.length < 20) {
			errs.comments = ' 20  ';
		}

		return errs;
	};

	const handleChange = (e) => {
		const { name, value } = e.target;
		setVal({ ...Val, [name]: value });
	};

	//  Val state  
	const handleRadio = (e) => {
		const { name } = e.target;
		const isChecked = e.target.checked;
		setVal({ ...Val, [name]: isChecked });
	};

	//  Val state  
	const handleCheck = (e) => {
		const { name } = e.target;
		let isChecked = false;
		const inputs = e.target.parentElement.querySelectorAll('input');

		//       true  
		inputs.forEach((el) => el.checked && (isChecked = true));
		setVal({ ...Val, [name]: isChecked });
	};

	//select  Val state  
	const handleSelect = (e) => {
		const { name } = e.target;
		const selected = e.target.value;
		setVal({ ...Val, [name]: selected });
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		setErr(check(Val));
	};

	useEffect(() => {
		const len = Object.keys(Err).length;
		if (len === 0 && Submit) {
			alert('  .');
			setVal(initVal);
			history.push('/');
		}
	}, [Err]);

	return (
		<Layout name={name} title={title} subTitle={subTitle} expCaption={expCaption}>
			<article className='joinCont'>
				<div className='inner'>
					<form action='result.html' method='get' id='member' onSubmit={handleSubmit}>
						<fieldset>
							<legend className='hidden'>    </legend>
							<div className='joinWrap'>
								<ul className='joinListWrap'>
									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' htmlFor='userid'>
												User ID
											</label>
											<div className='joinInput'>
												<input
													type='text'
													name='userid'
													id='userid'
													placeholder=' .'
													onChange={handleChange}
												/>
												<p className='err'>{Err.userid}</p>
											</div>
										</div>
									</li>

									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' htmlFor='pwd1'>
												Password
											</label>
											<div className='joinInput'>
												<input
													type='password'
													name='pwd1'
													id='pwd1'
													placeholder=' .'
													onChange={handleChange}
												/>
												<p className='err'>{Err.pwd1}</p>
											</div>
										</div>
									</li>

									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' htmlFor='pwd2'>
												RePassword
											</label>
											<div className='joinInput'>
												<input
													type='password'
													name='pwd2'
													id='pwd2'
													placeholder=' .'
													onChange={handleChange}
												/>
												<p className='err'>{Err.pwd2}</p>
											</div>
										</div>
									</li>

									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' htmlFor='name'>
												Name
											</label>
											<div className='joinInput'>
												<input
													type='text'
													name='name'
													id='name'
													placeholder=' '
													onChange={handleChange}
												/>
												<p className='err'>{Err.name}</p>
											</div>
										</div>
									</li>

									<li className='joinList'>
										<div className='joinBox'>
											<p className='joinTitle'>Education</p>
											<div className='joinInput'>
												<div className='eduBox'>
													<select name='edu' id='edu' onChange={handleSelect}>
														<option value=''></option>
														<option value='elementary school'> </option>
														<option value='middle school'> </option>
														<option value='high school'> </option>
														<option value='college'> </option>
													</select>
												</div>
												<p className='err'>{Err.name}</p>
											</div>
										</div>
									</li>

									<li className='joinList'>
										<div className='joinBox'>
											<p className='joinTitle'>Gender</p>
											<div className='joinInput'>
												<div className='radioBox'>
													<div className='joinRadio'>
														<input
															type='radio'
															name='gender'
															id='male'
															value='male'
															onChange={handleRadio}
														/>
														<label htmlFor='male'>Male</label>
													</div>
													<div className='joinRadio'>
														<input
															type='radio'
															name='gender'
															id='female'
															value='female'
															onChange={handleRadio}
														/>
														<label htmlFor='female'>Female</label>
													</div>
												</div>
												<p className='err'>{Err.gender}</p>
											</div>
										</div>
									</li>
									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' label htmlFor='email'>
												Email
											</label>
											<div className='joinInput'>
												<input
													type='text'
													name='email'
													id='email'
													placeholder=' .'
													onChange={handleChange}
												/>
												<p className='err'>{Err.email}</p>
											</div>
										</div>
									</li>
									<li className='joinList'>
										<div className='joinBox'>
											<p className='joinTitle'>Favorite</p>
											<div className='joinInput'>
												<div className='joinCheck'>
													<label htmlFor='html'>
														<input
															type='checkbox'
															name='favorite'
															id='html'
															value='html'
															onChange={handleCheck}
														/>
														<span className='checkIcon'></span>
														<span className='checkTxt'>html</span>
													</label>
												</div>
												<div className='joinCheck'>
													<label htmlFor='css'>
														<input
															type='checkbox'
															name='favorite'
															id='css'
															value='css'
															onChange={handleCheck}
														/>
														<span className='checkIcon'></span>
														<span className='checkTxt'>Css</span>
													</label>
												</div>
												<div className='joinCheck'>
													<label htmlFor='javascript'>
														<input
															type='checkbox'
															name='favorite'
															id='javascript'
															value='javascript'
															onChange={handleCheck}
														/>
														<span className='checkIcon'></span>
														<span className='checkTxt'>Javascript</span>
													</label>
												</div>
												<div className='joinCheck'>
													<label htmlFor='react'>
														<input
															type='checkbox'
															name='favorite'
															id='react'
															value='react'
															onChange={handleCheck}
														/>
														<span className='checkIcon'></span>
														<span className='checkTxt'>React</span>
													</label>
												</div>
												<div className='joinCheck'>
													<label htmlFor='vue'>
														<input
															type='checkbox'
															name='favorite'
															id='vue'
															value='vue'
															onChange={handleCheck}
														/>
														<span className='checkIcon'></span>
														<span className='checkTxt'>Vue</span>
													</label>
												</div>
												<p className='err'>{Err.favorite}</p>
											</div>
										</div>
									</li>
									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' label htmlFor='comments'>
												Comments
											</label>
											<div className='joinInput'>
												<textarea
													name='comments'
													id='comments'
													placeholder='  '
													onChange={handleChange}
												></textarea>
												<p className='err'>{Err.comments}</p>
											</div>
										</div>
									</li>
									<li className='joinList'>
										<div className='joinBox'>
											<label className='joinTitle' label htmlFor='submit'>
												button
											</label>
											<div className='joinInput'>
												<input type='submit' value='submit' onClick={() => setSubmit(true)} />
											</div>
										</div>
									</li>
								</ul>
							</div>
						</fieldset>
					</form>
				</div>
			</article>
		</Layout>
	);
}

export default Join;
